.sidebar{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: antiquewhite;
    color: #030014;
    z-index: 20;

    .bg{
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        width: 400px;
        background: rgb(244, 198, 241);

        .links{
            position: absolute;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 20px;

            a{
                font-size: 40px;
            }
        }
    }
    button{

        width: 50px;
        height: 50px;
        border-radius: 50%;
        position: fixed;
        top: 27px;
        left: 37px;
        background-color: transparent;
        border: none;
        cursor: pointer;
    }
}